import Head from 'next/head';
import Image from 'next/image';
import heroimg from "./images/heroimg.png";
import heroimg2 from "./images/heroimg2.png";

export default function Home() {
  return (
    <div className="min-h-screen">
      <Head>
        <title>LookLikes</title>
      </Head>

      <header className=" ">
        <div className="container mx-auto  px-6 flex items-center justify-between">
          <p className="text-9xl Jomhuria-regular text-purple-600">LOOK <span className="text-teal-400">LIKES</span></p>
        </div>
      </header>

      <main className="container mx-auto px-6">
        
        {/* Hero Section */}

        <section className="flex flex-col md:flex-row items-center gap-10">
          <div className="md:w-1/2 text-center md:text-left">
            <h1 className="Jomhuria-regular text-8xl text-gray-800">An ERP System to Run Your Entire Business</h1>
            <p className="mt-4 text-gray-600 poppins-regular text-3xl">
              Manage your business, from sales and invoicing, your books, your clientele and workforce to your inventory and operations.
            </p>
            <div className="mt-6 flex justify-center md:justify-start gap-4">
              <button className="poppins-extrabold text-2xl bg-purple-600 text-white py-2 px-4 rounded hover:bg-purple-700">Contact Us</button>
              <button className="poppins-extrabold text-2xl bg-white border-purple-600 border-solid border-4 rounded-lg text-purple-600 py-2 px-4 ">Watch Video</button>
            </div>
          </div>
          <div className="md:w-1/2 ">
            <div className="rounded-full overflow-hidden shadow-lg w-full max-w-[600px]"style={{height:"auto"}}>
              <Image src={heroimg} alt="Decorative"  className="absolute right-0 top-0  z-10 w-1/2 h-screen" />
              <Image src={heroimg2} alt="Decorative"  className="absolute right-px top-0  w-1/2 h-screen" />
            </div>
          </div>
        </section>

        {/* About Section */}
        <section className="" style={{marginTop:"10vh"}}>
          <div className="text-center">
          <p className="text-9xl Jomhuria-regular text-purple-600">LOOK <span className="text-teal-400">LIKES</span></p>
            <p className="mt-4 text-gray-600 poppins-medium text-3xl" >
              <span className='poppins-black text-3xl bg-gradient-to-r from-purple-500 to bg-teal-400 text-transparent bg-clip-text'>LookLikes</span> is here to improve the way brands handle their merchandising, sales and promotion efforts by offering a field force
              management solution that optimizes every step of the process, every second of the day.
            </p>
          </div>
        </section>
      </main>

     
    </div>
  );
}
